.contact-page{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #1a1616;
    color: #f9f4f4;
    box-sizing: border-box;
}

.form-container {
    width: 40%;
    background: linear-gradient(#212121, #212121) padding-box,
                linear-gradient(145deg, transparent 0%,#e74c3c, #f9f4f4) border-box;
    border-color: #e74c3c;
    border: 3px solid transparent;
    padding: 32px 24px;
    font-size: 14px;
    font-family: inherit;
    color: white;
    display: flex;
    flex-direction: column;
    gap: 20px;
    box-sizing: border-box;
    border-radius: 16px;
  }
  
  .form-container button:active {
    scale: 0.95;
  }
  
  .form-container .form {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .form-container .form-group {
    display: flex;
    flex-direction: column;
    gap: 2px;
  }
  
  .form-container .form-group label {
    display: block;
    margin-bottom: 5px;
    color: #717171;
    font-weight: 600;
    font-size: 14px;
  }
  
  .form-container .form-group input {
    width: 100%;
    padding: 12px 16px;
    border-radius: 8px;
    color: #fff;
    font-family: inherit;
    background-color: transparent;
    border: 1px solid #414141;
  }
  
  .form-container .form-group textarea {
    width: 100%;
    padding: 12px 16px;
    border-radius: 8px;
    resize: none;
    color: #fff;
    height: 150px;
    border: 1px solid #414141;
    background-color: transparent;
    font-family: inherit;
  }
  
  .form-container .form-group input::placeholder {
    opacity: 0.5;
  }
  
  .form-container .form-group input:focus {
    outline: none;
    border-color: #e81cff;
  }
  
  .form-container .form-group textarea:focus {
    outline: none;
    border-color: #e81cff;
  }
  
  .form-container .form-submit-btn {
    /* display: flex;
    align-items: flex-start;
    justify-content: center;
    align-self: flex-start; */
    font-family: inherit;
    color: #717171;
    font-weight: 600;
    width: 40%;
    background: #313131;
    border: 1px solid #414141;
    padding: 12px 16px;
    font-size: inherit;
    gap: 8px;
    margin-top: 8px;
    cursor: pointer;
    border-radius: 6px;
  }
  
  .form-container .form-submit-btn:hover {
    background-color: #fff;
    border-color: #fff;
  }

  #name{
    animation: slideDown 1s ease-out forwards;
    /* animation-delay: 0.5s; */
    opacity: 0;
  }

  #email{
    animation: slideDown 1s ease-out forwards;
    animation-delay: 0.5s;
    opacity: 0;
  }

  #message{
    animation: slideDown 1s ease-out forwards;
    animation-delay: 1s;
    opacity: 0;
  }

  #form-button{
    animation: slideDown 0.8s ease-out forwards;
    animation-delay: 1.5s;
    opacity: 0;
  }
  /* Keyframe Animation¡s */
  @keyframes slideDown {
    from {
        transform: translateY(-100%);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
  }
  
  
  
  